import { JSONable } from "../util/JSONable";
import { Packet } from "./Packet";
import { Kernel } from "./Kernel";
import { EntityAction } from "./EntityAction";

/**
 * 
 * 
 */
export class Entity extends JSONable {

    /**
     * 
     * @param {number} lifespan 
     */
    constructor(lifespan) {
        
        super();

        /**
         *  ID 
         * @protected
         * @type {number}
         */
        this.id = 0;

        /**
         * 
         * @type {number}
         */
        this.timestamp = Date.now();

        /**
         * 
         * @type {number}
         */
        this.expiry = this.timestamp + (lifespan !== undefined) ? lifespan : 10 * 60 * 1000;

        /**
         * 
         * @private
         * @type {object}
         */
        this.context = null;

        /**
         * 
         * @protected
         * @type {string}
         */
        this.moduleName = null;
    }

    /**
     *  ID 
     * @returns {number}  ID 
     */
    getId() {
        return this.id;
    }

    /**
     * 
     * @returns {number} 
     */
    getTimestamp() {
        return this.timestamp;
    }

    /**
     * 
     * @returns {number} 
     */
    getExpiry() {
        return this.expiry;
    }

    /**
     * 
     * @param {object} context 
     */
    setContext(context) {
        this.context = context;
    }

    /**
     * 
     * @returns {object} 
     */
    getContext() {
        return this.context;
    }

    /**
     * 
     * @returns {boolean}  {@linkcode true}  {@linkcode false} 
     */
    overdue() {
        return (this.expiry > Date.now());
    }

    /**
     * 
     * @param {JSON} data 
     */
    broadcast(data) {
        if (null == this.moduleName) {
            return false;
        }

        let id = this.id;
        let pipeline = Kernel.gsInstance.getPipeline('Cell');
        if (null == pipeline) {
            return false;
        }

        let packet = new Packet(EntityAction.Broadcast, {
            "id" : id,
            "data" : data
        });
        pipeline.send(this.moduleName, packet);

        return true;
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        return super.toJSON();
    }

    /**
     * @inheritdoc
     */
    toCompactJSON() {
        return super.toCompactJSON();
    }
}
