import { VideoDimension } from "./VideoDimension";
import { JSONable } from "../util/JSONable";

/**
 * 
 */
export class MediaConstraint extends JSONable {

    /**
     * @param {boolean} videoEnabled  Video 
     * @param {boolean} audioEnabled  Audio 
     */
    constructor(videoEnabled, audioEnabled) {
        super();

        /**
         * 
         * @type {boolean}
         */
        this.videoEnabled = videoEnabled;

        /**
         * 
         * @type {boolean}
         */
        this.audioEnabled = audioEnabled;


        this.dimension = VideoDimension.VGA;
    }

    /**
     * 
     * @param {VideoDimension} dimension 
     */
    setVideoDimension(dimension) {
        this.dimension = dimension;
    }

    /**
     * 
     * @returns {VideoDimension} 
     */
    getVideoDimension() {
        return this.dimension;
    }

    /**
     * 
     * @returns {JSON}  JSON 
     */
    getConstraints() {
        let json = {};
        json.audio = this.audioEnabled;
        if (this.videoEnabled) {
            json.video = this.dimension.constraints;
        }
        else {
            json.video = false;
        }
        return json;
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        json.audio = this.audioEnabled;
        json.video = this.videoEnabled;
        json.dimension = this.dimension;
        return json;
    }

    /**
     *  {@link MediaConstraint} 
     * @param {JSON} json  {@linkcode MediaConstraint}  JSON 
     * @returns {MediaConstraint}  {@link MediaConstraint} 
     */
    static create(json) {
        let mc = new MediaConstraint(json.video, json.audio);
        mc.dimension = json.dimension;
        return mc;
    }
}
