import { Entity } from "../core/Entity";

/**
 * 
 */
export class FileLabel extends Entity {

    /**
     * @param {number} id  ID 
     * @param {string} domain 
     */
    constructor(id, domain) {
        super();

        /**
         *  ID 
         * @type {number}
         */
        this.id = id;

        /**
         * 
         * @type {string}
         */
        this.domain = domain;

        /**
         *  ID 
         * @type {number}
         */
        this.ownerId = 0;

        /**
         * 
         * @type {string}
         */
        this.fileName = null;

        /**
         * 
         * @type {number}
         */
        this.fileSize = 0;

        /**
         * 
         * @type {number}
         */
        this.completedTime = 0;

        /**
         * 
         * @type {number}
         */
        this.expiryTime = 0;

        /**
         * 
         * @type {string}
         */
        this.fileCode = null;

        /**
         * 
         * @type {string}
         * @see {@link FileType}
         */
        this.fileType = 'unknown';

        /**
         *  MD5 
         * @type {string}
         */
        this.md5Code = null;

        /**
         *  SHA1 
         * @type {string}
         */
        this.sha1Code = null;

        /**
         *  URL 
         * @type {string}
         */
        this.fileURL = null;

        /**
         *  URL 
         * @type {string}
         */
        this.fileSecureURL = null;
    }

    /**
     * @returns {string} 
     */
    getDomain() {
        return this.domain;
    }

    /**
     * @returns {number}  ID 
     */
    getOwnderId() {
        return this.ownerId;
    }

    /**
     * @returns {string} 
     */
    getFileName() {
        return this.fileName;
    }

    /**
     * @returns {number} 
     */
    getFileSize() {
        return this.fileSize;
    }

    /**
     * @returns {string} 
     */
    getFileCode() {
        return this.fileCode;
    }

    /**
     * @returns {number} 
     */
    getCompletedTime() {
        return this.completedTime;
    }

    /**
     * @returns {number} 
     */
    getExpiryTime() {
        return this.expiryTime;
    }

    /**
     * @returns {string} 
     */
    getFileType() {
        return this.fileType;
    }

    /**
     * @returns {string}  MD5 
     */
    getMD5Code() {
        return this.md5Code;
    }

    /**
     * @returns {string}  SHA1 
     */
    getSHA1Code() {
        return this.sha1Code;
    }

    /**
     * @returns {string}  URL 
     */
    getFileURL() {
        return this.fileURL;
    }

    /**
     * @returns {string}  SSL URL 
     */
    getFileSecureURL() {
        return this.fileSecureURL;
    }

    /**
     *  JSON 
     * @returns {JSON}  JSON  {@link FileLabel} 
     */
    toJSON() {
        let json = super.toJSON();
        json.id = this.id;
        json.domain = this.domain;
        json.fileCode = this.fileCode;
        json.ownerId = this.ownerId;
        json.fileName = this.fileName;
        json.fileSize = this.fileSize;
        json.completed = this.completedTime;
        json.expiry = this.expiryTime;
        json.fileType = this.fileType;
        if (null != this.md5Code) json.md5 = this.md5Code;
        if (null != this.sha1Code) json.sha1 = this.sha1Code;
        if (null != this.fileURL) json.fileURL = this.fileURL;
        if (null != this.fileSecureURL) json.fileSecureURL = this.fileSecureURL;
        return json;
    }

    /**
     * @inheritdoc
     */
    toCompactJSON() {
        return this.toJSON();
    }

    /**
     *  JSON  {@link FileLabel} 
     * @param {JSON} json  JSON 
     * @returns {FileLabel}  {@link FileLabel} 
     */
    static create(json) {
        let label = new FileLabel(json.id, json.domain);
        label.fileCode = json.fileCode;
        label.ownerId = json.ownerId;
        label.fileName = json.fileName;
        label.fileSize = json.fileSize;
        label.completedTime = json.completed;
        label.expiryTime = json.expiry;
        label.fileType = json.fileType;
        label.md5Code = (undefined !== json.md5) ? json.md5 : null;
        label.sha1Code = (undefined !== json.sha1) ? json.sha1 : null;
        label.fileURL = (undefined !== json.fileURL) ? json.fileURL : null;
        label.fileSecureURL = (undefined !== json.fileSecureURL) ? json.fileSecureURL : null;
        return label;
    }
}
