import cell from "@lib/cell-lib";
import { Contact } from "./Contact";
import { ContactService } from "./ContactService";
import { GroupState } from "./GroupState";
import { Self } from "./Self";
import { AuthService } from "../auth/AuthService";

/**
 * 
 */
export class Group extends Contact {

    /**
     * 
     * @param {ContactService} service 
     * @param {Contact} owner 
     * @param {number} [id]  ID 
     * @param {string} [name] 
     * @param {string} [domain] 
     */
    constructor(service, owner, id, name, domain) {
        super((undefined === id) ? cell.Utils.generateSerialNumber() : id,
            name, (undefined === domain) ? AuthService.DOMAIN : domain);

        /**
         * 
         * @protected
         * @type {ContactService}
         */
        this.service = service;

        /**
         * 
         * @protected
         * @type {Contact}
         */
        this.owner = owner;

        /**
         * 
         * @protected
         * @type {number}
         */
        this.creationTime = 0;

        /**
         * 
         * @protected
         * @type {number}
         */
        this.lastActiveTime = 0;

        /**
         *  ID 
         * @protected
         * @type {Array<Contact>}
         */
        this.memberList = [ owner ];

        /**
         * 
         * @protected
         * @type {number}
         */
        this.state = GroupState.Normal;
    }

    /**
     * 
     * @param {Contact} newOwner 
     * @param {function} [handleSuccess] ({@linkcode group}:{@link Group}) 
     * @param {function} [handleError] ({@linkcode group}:{@link Group}) 
     * @returns {boolean} 
     */
    changeOwner(newOwner, handleSuccess, handleError) {
        if (!(newOwner instanceof Contact)) {
            if (handleError) {
                handleError(this);
            }
            return false;
        }

        return this.service.modifyGroup(this, newOwner, null, null, handleSuccess, handleError);
    }

    /**
     * 
     * @param {string} name 
     * @param {function} [handleSuccess] ({@linkcode group}:{@link Group}) 
     * @param {function} [handleError] ({@linkcode group}:{@link Group}) 
     * @returns {boolean} 
     */
    modifyName(name, handleSuccess, handleError) {
        if (name == this.name) {
            if (handleError) {
                handleError(this);
            }
            return false;
        }

        return this.service.modifyGroup(this, null, name, null, handleSuccess, handleError);
    }

    /**
     * 
     * @param {JSON|object} context 
     * @param {function} [handleSuccess] ({@linkcode group}:{@link Group}) 
     * @param {function} [handleError] ({@linkcode group}:{@link Group}) 
     * @returns {boolean} 
     */
    modifyContext(context, handleSuccess, handleError) {
        return this.service.modifyGroup(this, null, null, context, handleSuccess, handleError);
    }

    /**
     * 
     * @returns {Contact} 
     */
    getOwner() {
        return this.owner;
    }

    /**
     * 
     * @param {Contact|number} contact 
     * @returns {boolean}  {@linkcode true} 
     */
    isOwner(contact) {
        let id = 0;
        if (contact instanceof Contact) {
            id = contact.getId();
        }
        else if (contact instanceof Self) {
            id = contact.getId();
        }
        else {
            id = contact;
        }

        return (id == this.owner.id);
    }

    /**
     * 
     * @returns {number} 
     */
    getCreationTime() {
        return this.creationTime;
    }

    /**
     * 
     * @returns {number} 
     */
    getLastActiveTime() {
        return this.lastActiveTime;
    }

    /**
     *  {@link GroupState} 
     * @returns {number}  {@link GroupState} 
     */
    getState() {
        return this.state;
    }

    /**
     * 
     * @returns {number} 
     */
    numMembers() {
        return this.memberList.length;
    }

    /**
     * 
     * @returns {Array<Contact>} 
     */
    getMembers() {
        return this.memberList.concat();
    }

    /**
     * 
     * @param {Array<Contact|number>} members  ID 
     * @param {function} [handleSuccess] ({@linkcode group}:{@link Group}, {@linkcode members}:Array, {@linkcode operator}:{@link Contact}) 
     * @param {function} [handleError] ({@linkcode group}:{@link Group}, {@linkcode members}:Array, {@linkcode operator}:{@link Contact}) 
     * @returns {boolean} 
     */
    addMembers(members, handleSuccess, handleError) {
        return this.service.addGroupMembers(this, members, handleSuccess, handleError);
    }

    /**
     * 
     * @param {Array<Contact|number>} members  ID 
     * @param {function} [handleSuccess] ({@linkcode group}:{@link Group}, {@linkcode members}:Array, {@linkcode operator}:{@link Contact}) 
     * @param {function} [handleError] ({@linkcode group}:{@link Group}, {@linkcode members}:Array, {@linkcode operator}:{@link Contact}) 
     * @returns {boolean} 
     */
    removeMembers(members, handleSuccess, handleError) {
        return this.service.removeGroupMembers(this, members, handleSuccess, handleError);
    }

    /**
     * 
     * @param {Contact|number} contact 
     * @returns {boolean}  {@linkcode true} 
     */
    hasMember(contact) {
        let id = 0;
        if (contact instanceof Contact) {
            id = contact.getId();
        }
        else {
            id = parseInt(contact);
        }

        for (let i = 0; i < this.memberList.length; ++i) {
            let member = this.memberList[i];
            if (member.getId() == id) {
                return true;
            }
        }

        return false;
    }

    /**
     *  memberList 
     * @private
     * @param {Contact} member 
     */
    _removeMember(member) {
        let id = member.getId();
        for (let i = 0; i < this.memberList.length; ++i) {
            let m = this.memberList[i];
            if (m.getId() == id) {
                this.memberList.splice(i, 1);
                return;
            }
        }
    }

    /**
     *  memberList 
     * @private
     * @param {Contact} member 
     */
    _replaceMember(member) {
        this._removeMember(member);
        this.memberList.push(member);
        if (this.owner.getId() == member.getId()) {
            this.owner = member;
        }
    }

    /**
     * @inheritdoc
     */
    toJSON() {
        let json = super.toJSON();
        delete json.devices;
        json.owner = this.owner.toCompactJSON();
        json.creation = this.creationTime;
        json.lastActive = this.lastActiveTime;
        json.state = this.state;
        json.members = [];
        for (let i = 0; i < this.memberList.length; ++i) {
            json.members.push(this.memberList[i].toCompactJSON());
        }
        return json;
    }

    /**
     * @inheritdoc
     */
    toCompactJSON() {
        let json = super.toCompactJSON();
        json.domain = this.domain;
        json.owner = this.owner.toCompactJSON();
        json.creation = this.creationTime;
        json.lastActive = this.lastActiveTime;
        json.state = this.state;
        return json;
    }

    /**
     *  JSON  {@link Group} 
     * @param {ContactService} service 
     * @param {JSON} json  {@link Group}  JSON 
     * @param {Contact} [owner] 
     * @returns {Group}  {@link Group} 
     */
    static create(service, json, owner) {
        if (undefined === owner) {
            owner = new Contact.create(json.owner, json.domain);
        }

        let group = new Group(service, owner, json.id, json.name, json.domain);
        group.creationTime = json.creation;
        group.lastActiveTime = json.lastActive;
        group.state = json.state;

        if (undefined !== json.members) {
            for (let i = 0; i < json.members.length; ++i) {
                let member = Contact.create(json.members[i], json.domain);
                if (!group.hasMember(member)) {
                    group.memberList.push(member);
                }
            }
        }

        if (undefined !== json.context) {
            group.context = json.context;
        }

        return group;
    }
}
